import React, {useState} from 'react';

export const AddTodo = ({addTodo}) => {
    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");
    const submit = (e)=>{
        e.preventDefault();
        if(!title || !desc){
            alert("Title or Description can not be empty")
        }
        addTodo(title,desc);

    }

    return (
        <div className="container my-3 text-center">
            <h3>Add a Todo</h3>
            <form onSubmit={submit}>
                <div className="mb-3">
                    <label htmlFor="title" className="form-label">Todo Title</label>
                    <input type="text" className="form-control" value={title} onChange={(e)=>setTitle(e.target.value)} id="title" aria-describedby="todoHelp"/>
                        <div id="emailHelp" className="form-text">Add Your ToDO</div>
                        </div>
                    <div className="mb-3">
                        <label htmlFor="desc" className="form-label">To Do Description</label>
                        <input type="text" className="form-control" value={desc} onChange={(e)=>setDesc(e.target.value)} id="desc"/>
                     </div>
                    <button type="submit" className="btn btn-sm btn-success">Submit</button>
            </form>
            </div>
    )
}
